* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

html,
body,
#root,
.calc {
  width: 100%;
  height: 100%;
}

#root {
  background-color: #fcf9e6;
  overflow: scroll;
}

.calc {
  border: 2px solid black;
  width: 330px;
  height: 400px;
  position: relative;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  display: grid;
  grid-template-columns: 100%;
  grid-template-rows: 20% 80%;
  grid-template-areas:
    "display"
    "container";
}

.footer {
  position: fixed;
  bottom: 0;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: #fcf9e6;
  border-top: 2px solid #a62b2b;
  width: 100%;
  height: 1.5rem;
}

.footer .bukulele-logo {
  margin: 1px;
  height: 80%;
  width: auto;
}
.footer p {
  font-size: 0.8rem;
  color: #a62b2b;
}
.footer a {
  height: 100%;
}

